/* btn */
.btn-wrap {
  position: relative;
}

.btn {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  width: fit-content;
  height: 4.6rem;
  padding: 1.6rem 3.2rem 1.6rem 2.4rem;
  border-radius: 6px;
  background: #FBFBFB;
  font-family: "Noto Sans CJK JP";
  font-weight: 700;
  font-size: 1.5rem;
  line-height: 3.2rem;
  letter-spacing: 0.08em;
  color: #3E3A39;
  text-align: left;
  white-space: nowrap;
  text-decoration: none;
  border: none;
  outline: none;
  transition: all 0.6s cubic-bezier(0.8, 0, 0.2, 1);
  overflow: hidden;
  cursor: pointer;
  z-index: 0;
}

@media only screen and (max-width: 768px) {
  .btn {
    max-width: 33.5rem;
    width: 100%;
    padding: 1.6rem 2.0rem 1.6rem 2.4rem;
  }
}

.btn span {
  position: relative;
  display: inline-block;
  color: inherit;
  text-decoration: none;
  transition: all 0.6s cubic-bezier(0.8, 0, 0.2, 1);
  padding-top: 0.2rem;
  z-index: 3;
}

.btn svg {
  position: relative;
  display: inline-block;
  width: 2.4rem;
  height: auto;
  text-decoration: none;
  margin-left: 2.0rem;
  z-index: 3;
}

.btn svg path {
  transition: all 0.6s cubic-bezier(0.8, 0, 0.2, 1);
}

.btn.bg-gray {
  background: #EDEDED;
}

.btn.out-link svg {
  transform: rotate(-45deg);
  transform-origin: center center;
}

.btn::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  display: block;
  background: #68d5f6;
  width: 100%;
  height: 100%;
  transition: transform 0.6s cubic-bezier(0.8, 0, 0.2, 1);
  transform-origin: right top;
  transform: scale(0, 1);
  z-index: 1;
}

.btn:hover {
  color: #FFFFFF;
}

.btn:hover svg path {
  stroke: #FFFFFF;
}

.btn:hover::before {
  transform-origin: left top;
  transform: scale(1, 1);
}

.btn-rightarrow, .btn-leftarrow {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  width: fit-content;
  min-width: 24rem;
  padding: 1.5rem 5.6rem;
  border-radius: 7rem;
  border: 1px solid #AEAEAE;
  background: #FFF;
  color: #333;
  font-family: "Noto Sans CJK JP";
  font-size: 1.6rem;
  font-style: normal;
  font-weight: 700;
  line-height: 1.6rem;
  /* 100% */
  text-align: left;
  white-space: nowrap;
  text-decoration: none;
  -webkit-transition: background 0.6s cubic-bezier(0.8, 0, 0.2, 1);
  transition: background 0.6s cubic-bezier(0.8, 0, 0.2, 1);
  overflow: hidden;
  cursor: pointer;
  z-index: 0;
}

@media only screen and (max-width: 768px) {
