  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 0;
  width: 100%;
  max-width: 100%;
}

.footer-fixed .phone-btn img {
  width: 100%;
  height: auto;
}

.footer-fixed .mail-btn {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  background: #FF7100;
  width: 100%;
  max-width: 100%;
  height: 6.0rem;
  text-decoration: none;
  outline: none;
  padding: 1.0rem 1.2rem;
  border: 2px solid #FF7100;
  transition: background 0.6s cubic-bezier(0.8, 0, 0.2, 1);
  overflow: hidden;
  cursor: pointer;
  z-index: 1;
}

.footer-fixed .mail-btn .btn-icon {
  position: relative;
  flex: 0 0 auto;
  width: 3.0rem;
  height: auto;
  z-index: 3;
}

.footer-fixed .mail-btn .btn-icon path {
  transition: all 0.6s cubic-bezier(0.8, 0, 0.2, 1);
}

.footer-fixed .mail-btn .btn-text {
  position: relative;
  flex: 0 0 auto;
  width: calc(100% - 3.0rem);
  font-family: "Noto Sans JP", sans-serif;
  font-size: 1.6rem;
  line-height: 2.4rem;
  font-weight: 900;
  letter-spacing: 0.04em;
  color: #FFFFFF;
  text-align: center;
  white-space: nowrap;
  text-decoration: none;
  padding-left: 1.0rem;
  z-index: 3;
}

.footer-fixed .mail-btn .btn-text small {
  position: relative;
  display: block;
  font-size: 1.2rem;
  font-weight: bold;
  line-height: 1.8rem;
  color: #FED517;
  transition: color 0.6s cubic-bezier(0.8, 0, 0.2, 1);
}

.footer-fixed .mail-btn .btn-text strong {
  position: relative;
  display: block;
  font-size: inherit;
  line-height: inherit;
  color: inherit;
  transition: color 0.6s cubic-bezier(0.8, 0, 0.2, 1);
}

@media only screen and (max-width: 768px) {
  .footer-fixed .mail-btn .btn-text strong {
    letter-spacing: 0;
  }
}

.footer-fixed .mail-btn::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  display: block;
  background: #FFFFFF;
  width: 100%;
  height: 100%;
  transition: transform 0.6s cubic-bezier(0.8, 0, 0.2, 1);
  transform-origin: right top;
  transform: scale(0, 1);
  z-index: 1;
}

.footer-fixed .mail-btn:hover .btn-icon path {
  fill: #FF7100;
}

.footer-fixed .mail-btn:hover .btn-text {
  color: #FF7100;
}

.footer-fixed .mail-btn:hover .btn-text small {
  color: #FF7100;
}

.footer-fixed .mail-btn:hover .btn-text strong {
  color: #FF7100;
}

.footer-fixed .mail-btn:hover::before {
  transform-origin: left top;
  transform: scale(1, 1);
}
